.page {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  scroll-behavior: smooth;

  &__body {
    background: $c-gray;
    margin: 0;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translatex(-100%);
    transform: transform 0.3s;

    &:target {
      transform: translatex(0);
    }
  }

  &__section {
    margin-bottom: 120px;
  }

  &__section-title {
    margin: 0 0 48px;

    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $color;

    @include onDesktop {
      margin-bottom: 64px;
      font-size: 48px;
      line-height: 48px;
    }
  }
}
